import { Component, OnInit } from '@angular/core';

//for popup modal starts 
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
//for popup modal endss 
//for the select option with filter starts
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
//for the select option with filter ends

@Component( {
    selector: 'app-get-hireslip-barcode-value',
    templateUrl: './get-hireslip-barcode-value.component.html',
    styleUrls: ['./get-hireslip-barcode-value.component.css']
} )
export class GetHireslipBarcodeValueComponent implements OnInit {

    closeResult: string;
    //summaryTable:any;



    constructor( private modalService: NgbModal ) { }
    control = new FormControl();
    reasonForReturnOptions = [
        { id: 1, label: 'ADD NEW' },
        { id: 2, label: 'Door Locked' },
        { id: 3, label: 'Vehicle Repair' },
        { id: 3, label: 'over load' }
    ];
    //for popup modal starts 
    open2( content ) {
        this.modalService.open( content, { centered: true } ).result.then(
            result => {
                this.closeResult = `Closed with: ${result}`;
            },
            reason => {
                this.closeResult = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }
    //for popup modal ends 
    //for the select option with filter starts

    transform( items: any[], searchTerm: string, labelKey?: string ): any {
        if ( !items || !searchTerm ) {
            return items;
        }

        return items.filter(
            item =>
                item[labelKey || 'label']
                    .toLowerCase()
                    .includes( searchTerm.toLowerCase() ) === true
        );
    }

    //for the select option with filter ends

    ngOnInit() {
    }


}
